body {
  background-image: linear-gradient(
    to top right,
    var(--gradient-bottom-left), var(--gradient-top-right)
  );
  height: 100vh;
  width: 100vw;
  @media only screen and (max-width:1200px) { 
    height: auto;
    width: auto;
    background-image: linear-gradient(
      to top,
      var(--gradient-bottom-left), var(--gradient-top-right)
    );
    
  }
}
